.image{
  position: relative;
  overflow: hidden;
}

@media(min-width:768px){

 .image{

    border-radius: 20px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
  }
}

@media(max-width:768px){

  .image{
    height: 30vh;
    width:65vw;
    object-fit: cover;
    border-radius: 1em;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.13);
  } 
}



.carousel-button {
  position: absolute;
  width: 1em;
  top: 0;

  z-index: 1000;
  border: none;
  background-color: rgb(254, 254, 255);
  /*mix-blend-mode: difference; /* Apply the inversion effect */
  opacity: 0.7;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.479);
}
.carousel-button:hover {
  opacity: 0.6;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.479);

}

.carousel-button.previous {
  left: 0.2em;
  top:30%;
  bottom:30%;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
}
.carousel-button.next {
  right: 0.2em;
  top:30%;
  bottom:30%;
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
}

.carousel-image{
  position: absolute;
}

.carousel-image.image-enlarged{
  animation: imageMoveBig 0.1s forwards;
}

.carousel-image.image-normal{
  animation: imageMoveNormal 0.5s forwards;
}

.hover-area{
  left:2em;
  right:2em;
  height:100%;
  position:absolute;
  z-index: 10;
}

/* Keyframes for enlarging image */
@keyframes imageMoveBig {
  0% {
    left:0em;
  }
  100% {
    left:8em;
  }
}

/* Keyframes for reverting to default size */
@keyframes imageMoveNormal {
  0% {
    left:8em;
  }
  30%{
    left:8em;
  }
  100% {
    left:0em;
  }
}