
.content {
  position: absolute;
  max-height: 85vh;
  top:15vh;
  z-index: 10;
  }
  
  
  /* Change the color of the scrollbar thumb */
    .content::-webkit-scrollbar-thumb {
      background-color: rgb(187, 38, 38);
    }
    
    /* Change the color of the scrollbar track */
    .content::-webkit-scrollbar-track {
      background-color: rgb(87, 4, 4);
    }





@media(min-width:768px){
.content {
  max-width: 100em;

    display: grid;
    align-items: center;
    gap: 5em;
    margin-bottom: 30vh;

      overflow-y: scroll;
      scroll-snap-type: y mandatory;
    
  }

  
  .navbar-background,
  .content::before {
    height: 35vh;
    width:45%;
    bottom:-100px;
    right: 10%;
    position: fixed;
  }

}

  @media(max-width:768px){
    .content{
      width:100vw;
      height: 80vh;
  
      top:15vh;
      align-items: center;

      display: flex;
      overflow-y: hidden;
      overflow-x: scroll;
      scroll-behavior: smooth;
      z-index: 100;
    }
   
    
    .navbar-background {
      height: 12em;
      width:45%;
      bottom:-100px;
      right: 10%;
      position: fixed;
    }



  }

  .title{
    width:100%;
    text-align: center;
    height:10vh;
    color:var(--projectTitle);
  }


         
  .navbar-background{

    background-color:rgba(240, 240, 240, 0.9);
    z-index: 15;
    position: fixed;

    border-radius: 0.4em;
    box-shadow: 0 4px 40px rgba(0, 0, 0, 0.13);
  }



  .content::before {
    content: "";
    backdrop-filter: blur(2px); /* Adjust blur amount as needed */
    z-index: -1; /* Make sure this is below the .navbar-background */
  }





  