.fade-in{
    opacity: 0;
    animation: fadeIn 0.7s 0s forwards;
  }

  .fade-in-late{
    opacity: 0;
    animation: fadeIn 0.7s 0.4s forwards;
  }


  @keyframes fadeIn {
    to{
      opacity: 1;
    }
  }



  .blob-overlay.home-state{
    animation: colorIn 0.7s 0s forwards;
  }
  .blob-overlay.project-state{
    animation: colorOut 0.1s 0s forwards;
  }
  @keyframes colorIn {
    from{
      background-color: var(--main-background);
      fill: #c4079500;
    }
    to{
      background-color: var(--main-background);
      fill: var(--accent-background);
    }}

  @keyframes colorOut {
    from{
      background-color: var(--main-background);
      fill: var(--accent-background);
    }
    to{
      background-color: var(--main-background);
      fill: #c4079500;
    }}



@media(min-width:768px){
  .navigation-bar.home-state {
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    position: fixed;
    z-index: 10;
    transition: top 0.5s 0.2s, left 0.5s 0.2s, transform 0.5s 0.2s;
  }
  
  .navigation-bar.project-state {
    top: 40%;
    left: 20%;
    transform: translate(10%, 40%) scale(0.5);
    transition: top 0.5s, left 0.5s, transform 0.5s;
  }
}


@media(max-width:768px){
  .navigation-bar.home-state {
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    position: fixed;
    z-index: 10;
    transition: top 0.5s 0.2s, left 0.5s 0.2s, transform 0.5s 0.2s;
  }
  
  .navigation-bar.project-state {
    top: 100%;
    left: 27%;
    transform: translateY(-80%) scale(0.5);
    transition: top 0.5s, left 0.5s, transform 0.5s;
    margin:0;
  }
}







/*defining the size of the navbar widgets when the screen becomes too small*/
@media(max-width:43em){
  ._Boom{
    width:20em;
  }

._RealF1{
    position: absolute;
    transform: translate(-50%,-64%);
    width:30vw;
  }

._3Dprinting{
    position: absolute;
    transform: translate(-45%,-93%);
    width:16em;
  }


._Me{
    position: absolute;
    transform: translate(-50%,-90%);
    width:20em;
    z-index: 100;
  }
}
@media(max-width:50em){
  ._Soldering{
    position: absolute;
    transform: translate(-50%,-55%);
    width:20vw;
  }
}
  @media(max-width:43em){
  ._Future{
    position: absolute;
    transform: translate(-50%,-85%);
    width:14vw;
  }
}



