.content-block{
  background-color: var(--contentblock-background);
  background-image: radial-gradient(var(--contentblock-background),#ec0000 1000%)
      
}


@media(min-width:768px){
    .content-block {
      height:30em;
      width:90%;
  
      z-index: 10;
      
      display: grid;
      
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
      border: 0.3em solid var(--contentBorder);
      border-radius: 2em;
    }



    /* Initial state */
.content-block {
  grid-template-columns: 20em 1fr;
}

/* Animation for enlarging image */
.content-block.image-enlarge {
  animation: imageEnlarge 0.1s forwards;
}

/* Animation for reverting to default size */
.content-block.image-normal {
  animation: imageToDefault 0.5s forwards;
}

/* Keyframes for enlarging image */
@keyframes imageEnlarge {
  from {
    grid-template-columns: 20em 1fr;
  }
  to {
    grid-template-columns: 30em 1fr;
  }
}

/* Keyframes for reverting to default size */
@keyframes imageToDefault {
  0% {
    grid-template-columns: 30em 1fr;
  }
  30%{
    grid-template-columns: 30em 1fr;
  }
  100% {
    grid-template-columns: 20em 1fr;
  }
}


    .text{
      width:60%;
      margin:15%;
      margin-left: 20%;
      text-align: center;
    }
  }
  
    @media(max-width:768px){

      .content-block{
        height:40em;
        padding:2em;
        padding-top: 0;
        padding-bottom: 0;
        border-radius: 2em;
        margin: 5vw;
        margin-top: 0;
        width:100vw;
  
        position: relative;
        
        z-index: 10;
        display: grid;
        grid-template-rows: 20em 1fr;
        scroll-margin-left: 20em;
        scroll-margin-right: 20em;
  
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
        border: 0.3em solid var(--contentBorder);
        scroll-snap-align: start;
  
      }

      .text{
        width:70%;
        margin:15%;
        margin-top: 0;
        text-align:justify;
      }
  
  
    }
  
    .text{
      color:var(--projectText);
    }

  
  
  
    





