/* navigationBar.css */

    /* Styles for the navigation bar container */
    .navigation-bar {
        position: absolute;

        top:0;
        left:0;
        right:0;
        bottom:0;
        margin:auto;
        
        display: grid;
        place-items: center;

        height: 40%;
        width: 80%;
        max-width: 800px;

        z-index: 9000;
    }


