/* LandingPage.css */

    /* Styles for the navigation bar container */

    .nameplate h1{
      color:var(--landingpage-title);
    }
    .nameplate{
      color:var(--landingpage-nameplate);
    }

    .project-page{
      overflow: hidden;
    }
    .landing-page{
      overflow: hidden;/*
      background-image: linear-gradient(to right, #F6F6F6, #F0F0F0);
      z-index: -10;*/

    }
 

    .blob-overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      background:var(--main-background);
      background-image: linear-gradient(45deg, rgba(0, 0, 0, 0.05) 25%, transparent 25%, transparent 75%, rgba(0, 0, 0, 0.05) 75%, rgba(0, 0, 0, 0.05) 100%);
      background-size: 0.3em 0.3em;
      opacity: 1;
      z-index: -1;
    }



    
    
    
    

@media(min-width:768px){
    .landing-page {
        justify-content: center;
        align-items: center;
        height: 100vh; /* Adjust the height as needed */
        max-width: 100%;
        overflow-x:hidden;
    }

      .nameplate{
        position: absolute;
        top:64%;
        width:70%;
        left:10%;
        display: grid;
        place-items: center;
      }

      .logoimg{
        position: absolute;
        left:11%;
        height:100px;   
        stroke-width: 100px;
      }
}

@media(max-width:768px){
  .landing-page {
      justify-content: center;
      align-items: center;
      height: 100vh; /* Adjust the height as needed */
      max-width: 100%;
      overflow-y:hidden;
  }

    .nameplate{
      position: absolute;
      top:64%;
      width:70%;
      left:10%;
      display: grid;
      place-items: center;
    }

    .logoimg{
      position: absolute;
      left:11%;
      height:100px;   
      stroke-width: 100px;
    }
}


.nameplate{
  z-index:15;

}

.portfolio_premise{
  font-family: 'Quicksand';
  font-weight: 10;
  max-width: 42em;
}


