body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Quicksand';

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  height: 100vh; /* Set the body height to 100% of the viewport height */
  /*overflow-x: hidden; /* Prevent scrollbars from appearing */
}

h1, h2, h3 {
  font-family: 'Nunito', sans-serif;
}


p{
  font-family:'Quicksand';
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



:root{
  --main-background:#FFF6DC;
  --accent-background:#ffc6ac;
  --text:#5c6875;
  --landingpage-title:var(--text);
  --landingpage-nameplate:var(--text);
  --projectTitle:var(--text);
  --projectSubject:var(--text);
  --projectText:var(--text);
  --contentblock-background:var(--accent-background);
  --contentBorder:var(--main-background);
}
